import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Form, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute } from '@angular/router';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { TypeOfMainDocument } from 'app/core/models/type-of-main-document.model';
import { UploadingDocumentFileSize } from 'app/core/models/uploading-document-file-size-model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-main-document',
  templateUrl: './type-of-main-document.component.html',
  styleUrls: ['./type-of-main-document.component.scss']
})
export class TypeOfMainDocumentComponent implements OnInit, OnDestroy {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfMainDocument>;
  dataSource1: MatTableDataSource<UploadingDocumentFileSize>;
  displayedColumns: string[];
  statusList = [...STATUSES];

  typeOfMainDocument = new TypeOfMainDocument();
  uploadingDocumentFileSize = new UploadingDocumentFileSize();

  typeOfMainDocumentList = [];

  typeOfMainDocumentForm: FormGroup;
  uploadingDocumentFileSizeForm: FormGroup;

  maintenanceType: any;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.maintenanceType = this.route.parent.snapshot.url[0].path;
    if (this.maintenanceType === 'type-of-main-documents') {
      this.displayedColumns = ['typeOfMainDocument', 'status'];
      this.getList();
    } else {
      this.displayedColumns = ['size'];
      this.getAllFileSize();
    }
    this.setFormValidator();
  }

  ngOnDestroy(): void {
    this.maintenanceType = null;
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfMainDocuments).subscribe((res: any) => {
      this.typeOfMainDocumentList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfMainDocument>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.typeOfMainDocuments, this.typeOfMainDocument).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  getTypeOfMainDocument(): void {
    this.apiService.findById(ENDPOINTS.typeOfMainDocuments, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfMainDocument = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.typeOfMainDocuments, this.typeOfMainDocument).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    if (this.maintenanceType === 'type-of-main-documents') {
      this.typeOfMainDocument.status = COMMON_FUNCTION.ACTIVE;
      this.typeOfMainDocumentForm = this.formBuilder.group({
        typeOfMainDocument: ['', Validators.required],
        status: [''],
      });
    } else {
      this.uploadingDocumentFileSizeForm = this.formBuilder.group({
        // id: ['',],
        fileSize: ['', Validators.required],
      });
    }

  }

  resetFormValidator(): void {
    if (this.maintenanceType === 'type-of-main-documents') {
      this.typeOfMainDocument = new TypeOfMainDocument();
      this.typeOfMainDocument.status = COMMON_FUNCTION.ACTIVE;
      this.typeOfMainDocumentForm.markAsUntouched();
      this.typeOfMainDocumentForm.markAsPristine();
    } else {
      this.uploadingDocumentFileSize = new UploadingDocumentFileSize();
      this.uploadingDocumentFileSizeForm.markAsUntouched();
      this.uploadingDocumentFileSizeForm.markAsPristine();
    }

  }


  //START OF FILE SIZE CONTROLLER

  getAllFileSize(): void {
    this.apiService.findAll(ENDPOINTS.uploadingDocumentFileSize).subscribe((res: any) => {
      this.uploadingDocumentFileSize = res.responseData.data;
      this.dataSource1 = new MatTableDataSource<UploadingDocumentFileSize>(res.responseData.data);
      this.dataSource1.paginator = this.formListComponent.paginator;
      this.dataSource1.sort = this.formListComponent.sort;
    });
  }

  getuploadingDocumentFileSize(): void {
    this.apiService.findById(ENDPOINTS.uploadingDocumentFileSize, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.uploadingDocumentFileSize = res.responseData.data;
        console.log(this.uploadingDocumentFileSize);
      }
    });
  }

  updateUploadingDocumentFileSize() {
    this.apiService.update(ENDPOINTS.uploadingDocumentFileSize, this.uploadingDocumentFileSize).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getAllFileSize();
      });

  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this type of main document?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.typeOfMainDocuments, this.typeOfMainDocument.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Type of main document successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
